"use client";

import Link from "next/link";
import { useMemo, useState } from "react";
import { useQuery } from "convex/react";
import { ArrowUpRight, Loader2, Search, Shield, Sparkles, Truck } from "lucide-react";
import { ThemeToggle } from "@/components/ThemeToggle";
import { ProductCard } from "@/components/ProductCard";
import type { PublicProduct } from "@/lib/types";

const highlights = [
  { icon: <Sparkles size={16} />, title: "Top izbor", desc: "Pažljivo odabrani modeli spremni za rad." },
  { icon: <Truck size={16} />, title: "Brza isporuka", desc: "Šaljemo odmah po potvrdi porudžbine." },
  { icon: <Shield size={16} />, title: "Sigurna kupovina", desc: "Transparentne cene i jasna specifikacija." },
];

export default function StorefrontPage() {
  const [search, setSearch] = useState("");
  const products = useQuery("products:listPublic", { search: search.trim() || undefined }) as
    | PublicProduct[]
    | undefined;
  const isLoading = products === undefined;
  const items = products ?? [];

  const stats = useMemo(
    () => ({
      variants: items.reduce((acc, item) => acc + (item.variants?.length ?? 0), 0),
      published: items.length,
    }),
    [items],
  );

  return (
    <div className="stack">
      <div className="glass hero">
        <div className="topbar">
          <div className="brand">
            <span className="brand__dot" />
            Alati Nexus
          </div>
          <div className="meta-row">
            <ThemeToggle />
            <div className="pill strong">{stats.published} proizvoda</div>
          </div>
        </div>

        <div className="hero__grid">
          <div className="hero__right">
            <p className="eyebrow">Premium alat</p>
            <h1 className="hero__title">Oštar izbor alata spreman za kupce.</h1>
            <p className="hero__subtitle">
              Izdvajamo samo proverene komade. Pronađi svoj alat, vidi cenu i naruči u par klikova.
            </p>
            <div className="search">
              <Search size={18} className="search__icon" />
              <input
                value={search}
                onChange={(event) => setSearch(event.target.value)}
                placeholder="Pretraži katalog..."
                aria-label="Pretraži proizvode"
              />
            </div>
            <div className="highlight-row">
              {highlights.map((item) => (
                <div key={item.title} className="highlight-card">
                  <div className="highlight-icon">{item.icon}</div>
                  <div>
                    <p className="highlight-title">{item.title}</p>
                    <p className="muted">{item.desc}</p>
                  </div>
                </div>
              ))}
            </div>
            <div className="cta-row">
              <Link href="#proizvodi" className="cta primary" scroll>
                Pogledaj ponudu <ArrowUpRight size={18} />
              </Link>
              <div className="pill ghost">Varijanti u katalogu: {stats.variants}</div>
            </div>
          </div>

          <div className="hero__right">
            <div className="glass hero-card">
              <div className="hero-card__content">
                <p className="eyebrow">Novi komadi</p>
                <h3 className="hero-card__title">Odmah dostupno</h3>
                <p className="muted">
                  Sve cene su istaknute, a fotografije su iz realnih postavki. Spremno za isporuku.
                </p>
                <div className="hero-card__stats">
                  <div>
                    <p className="label">Na stanju</p>
                    <p className="stat">{stats.published}</p>
                  </div>
                  <div>
                    <p className="label">Varijante</p>
                    <p className="stat">{stats.variants}</p>
                  </div>
                </div>
                <Link href="#proizvodi" className="cta" scroll>
                  Idi na katalog <ArrowUpRight size={16} />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>

      <section className="section" id="proizvodi">
        <div className="section__header">
          <div className="section__title">
            <Sparkles size={18} />
            Katalog
          </div>
          <div className="pill">{isLoading ? "Učitavanje..." : `${items.length} artikala spremno`}</div>
        </div>

        {isLoading ? (
          <div className="loading">
            <Loader2 className="animate-spin" size={18} />
            Učitavanje proizvoda...
          </div>
        ) : items.length === 0 ? (
          <div className="empty">Nema proizvoda za prikaz. Dodaj u panelu i pojaviće se ovde.</div>
        ) : (
          <div className="product-grid">
            {items.map((product) => (
              <ProductCard key={product.id} product={product} />
            ))}
          </div>
        )}
      </section>
    </div>
  );
}
